import {ErrorContext, FunctionWrapper} from "@service/flow/function-wrapper";
import {LoadingFlowWrapper} from "@service/flow/loading-flow-wrapper.data";
import {ScreenReaderAnnouncerService, SrEvent, SrMessageType} from "@service/screen-reader-announcer.service";
import {Reflection} from "@util/reflection";
import {GlobalLogger} from "../../../../vsan-app.module";

/**
 * Method decorator that does the following:
 *  - watches for errors and if such appear:
 *       - the error is logged in the browser's console
 *       - the error is logged in the H5C server log
 *       - the related @ErrorMessage fields are populated
 *       - the related @ErrorHandler methods are invoked
 *  - Updates the @LoadingIndicator decorated fields
 *  - Invokes the screen reader service once the loading operation completes successfully
 */
export function Loader(params?: LoaderParams): MethodDecorator {
   // If no params are passed, initialize the variable in order to avoid multiple null-checks
   params = params || {};
   return (targetClass: any, functionName: string, descriptor: PropertyDescriptor): void => {
      // Use the component name as tag in order easily find where the error was thrown.
      const tag: string = Reflection.getClassName(targetClass);
      const loaderName: string = params.name;
      const originalFn: any = descriptor.value;

      descriptor.value = function (): any {
         const loadingFlowWrapper: LoadingFlowWrapper = new LoadingFlowWrapper(this, loaderName);
         return (new FunctionWrapper(originalFn))
               .preExecute(() => {
                  loadingFlowWrapper.error = undefined;
                  loadingFlowWrapper.incrementLoads();
                  ScreenReaderAnnouncerService.invoke(
                        params.preLoadSrMessage || params.srMessage,
                        params.srMessageType, SrEvent.LOADING);
               })
               .postExecute(() => {
                  ScreenReaderAnnouncerService.invoke(params.srMessage, params.srMessageType, SrEvent.LOADED);
               })
               .onError((ex: any) => {
                  processError(loadingFlowWrapper, tag, ex);
                  const rethrow: boolean = shouldRethrow(loadingFlowWrapper, params);
                  if (!rethrow) {
                     ex = invokeErrorHandler(loadingFlowWrapper, ex);
                  }
                  return new ErrorContext(rethrow, ex);
               })
               .finally(() => {
                  loadingFlowWrapper.decrementLoads();
               })
               .invoke(loadingFlowWrapper.backingObject, arguments);
      };
   };
}

/**
 * Parameters for the @Loader decorator
 */
export interface LoaderParams {

   /**
    * The name of the loader by which will be resolved te @LoadingIndicator, @ErrorHandler and @ErrorMessage
    */
   name?: string;

   /**
    * Indicates if the error should be rethrown after being processed by the @ErrorHandlers
    */
   rethrow?: boolean;

   /**
    * Used along with srMessageType to create the screen reader message to be announced after the loading
    * operation completes successfully.
    * If preLoadSrMessage is not set, srMessage will also be announced when the loading operation starts.
    */
   srMessage?: string;

   /**
    * Used when the message in preExecute() should be different than the one in postExecute().
    * If not set, srMessage will be used in both places.
    * Should be given a value when the srMessageType is set to CUSTOM,
    * because otherwise the exact same message will be announced in preExecute() and postExecute().
    */
   preLoadSrMessage?: string;

   /**
    * The screen reader message type. Used for creating an appropriate message.
    */
   srMessageType?: SrMessageType;
}

/**
 * Handles the given error by:
 *  - logging the error in the browsers console
 *  - logging the error in the H5C backend log
 *  - updates all @ErrorMessage fields
 */
function processError(wrapper: LoadingFlowWrapper, tag: string, ex: any): void {
   // Log the error to the backend
   GlobalLogger.error(tag, ex);

   // Cache the error so it can be retrieved by @ErrorMessage decorated fields
   wrapper.error = ex;
}

/**
 * Invokes the error handler (if defined)
 */
function invokeErrorHandler(wrapper: LoadingFlowWrapper, ex: any): any {
   const handler: any = wrapper.errorHandler;
   return !!handler
         ? handler.call(wrapper.backingObject, ex)
         : null;
}

/**
 * Reject the promise only if:
 * 1. there are no handlers declared
 * 2. the components has explicitly requested the error to be rethrown
 */
function shouldRethrow(wrapper: LoadingFlowWrapper, params: LoaderParams): boolean {
   return !wrapper.errorHandler && !wrapper.errorMessageGetter
         || !!params.rethrow;
}
